.screen {
  width: 95.7%;
  height: 40px;
  margin-left: 1%;
  font-size: 26px;
  font-weight: 500;
  text-align: right;
  border: 1px solid #c6c6c6; }

.buttons {
  width: 23%;
  height: 40px;
  float: left;
  font-weight: 500;
  font-size: 20px;
  text-align: center;
  line-height: 40px;
  margin-top: 2px;
  background: #f1f1f1;
  border: 1px solid #dedede;
  margin-left: 1%;
  border-radius: 2px; }

.buttons:hover {
  border: 1px solid #aaa;
  cursor: pointer; }

.buttonsEqual {
  background: #4a8cf7;
  color: #fff; }

.buttonsSymbol {
  background: #d3d3d3; }



